import confirm from "@/components/confirm";//确定框
import bottomTopPanel from '@/components/bottom-top-panel';//授权弹窗
export default {
	components: {
		confirm,
		bottomTopPanel,
	},
	data() {
		return {
			openId:"",
			// 默认不显示登记按钮
			register:false,
			// 申请通行证
			registerBtnName:"申请通行证"
		};
	},
	onLoad() {
		// 如果登录过,直接获取openid
		if(!this.$custom.isEmpty(this.$store.state.openId)){
			this.loadJsCode();
		}
		// this.showCustomPopupClick();
	},
	methods: {
		// 登记
		navRegister(){
			if(this.$custom.isEmpty(this.$store.state.openId)){
				this.showCustomPopupClick();
				return false;
			}
			this.navToPage('EPass/EPassIndex');
		},
		// 个人信息
		navPersonInfo(){
			if(this.$custom.isEmpty(this.$store.state.openId)){
				this.showCustomPopupClick();
				return false;
			}
			this.navToPage('scancode/person-info');
		},
		// 通行记录
		navExitRecord(){
			if(this.$custom.isEmpty(this.$store.state.openId)){
				this.showCustomPopupClick();
				return false;
			}
			this.navToPage('scancode/current-record');
		},
		// 我的通行证
		navPersonZJ(){
			if(this.$custom.isEmpty(this.$store.state.openId)){
				this.showCustomPopupClick();
				return false;
			}
			this.navToPage('EPass/qrcode');
		}, 
		
		// 加载小程序code
		loadJsCode(){
			let _this = this;
			uni.login({
				provider: 'weixin',
				success: function (res) {
					_this.setOpenid(res.code);
				}
			});
		},
		// 设置openid 供全局调用
		async setOpenid(code){
			let res = await this.$request.post(this.$api.getOpenID,{code:code});
			if(res.appCode === 1){
				this.openId = res.data.openid;
				// 保存openid到状态
				this.$store.dispatch('refreshOpenId',this.openId);
				if(this.$custom.isEmpty(res.data.openid)){
					wx.hideLoading();
					// 如果没有登录 提示
					if(this.$store.state.registerStatus == 0){
						this.$refs['Message'].error("授权登录失败");
					}
				}else{
					// 如果没有登录 提示
					if(this.$store.state.registerStatus == 0){
						this.$refs['Message'].success("授权登录成功");
						// 跳转页面
						this.navDistPage(res.data.openid);
					}
					
				}
			}
		},
		// 跳转页面(区分管理员和用户)
		async navDistPage(openId){
			let res = await this.$request.post(this.$api.isAdmin,{"openId":openId});
			// 1是管理员  0普通住户
			// 保存用户权限到状态
			this.$store.dispatch('refreshUserType',res.appCode);
			if(res.appCode === 1){
				// 跳转页面
				this.$routes.redTo("/pages/index/manager");
			}else{
				// 请求判断是否登记过
				let result = await this.$request.post(this.$api.getMyInfo,{"openId":this.$store.state.openId});
				// 1登记
				if(result.appCode == 1){
					// 保存用户登记数据到状态
					this.$store.dispatch('refreshRegisterData',result.data);
					
					//checkStatus  1代表登记过，但是审核不通过，需要重新登记
					if(result.data.checkStatus == 1){
						this.registerBtnName = "重新申请通行证";
						this.register = true;
					}else{
						this.registerBtnName = "申请通行证";
						this.register = false;
					}
				}else{
					/// -1未登记
					this.register = true;
					this.registerBtnName = "申请通行证";
					// 保存用户登记数据到状态
					this.$store.dispatch('refreshRegisterData',"");
				}
			}
			wx.hideLoading();
		},
		//详情页
		navToPage(url) {
			this.$routes.navTo('/pages/'+url);
		},
		// 显示提示
		showConfirm(content){
			this.$refs.confirm.show({title:"注意",content:content,sure:"确定"});
		},
		// 确认提交输入框(通过审核)
		confirm(){
			this.$refs.confirm.hide();
		},
		// 显示授权窗口
		showCustomPopupClick() {
			this.$refs.bottomTopPanel.showCustom();
		},
		// 隐藏授权窗口
		cancelButton() {
			this.$refs.bottomTopPanel.maskClick();
		},
		// 允许授权
		sureButton(){
			uni.showLoading({
				mask:true,
			    title: "正在加载中"
			});
			// 获取openid
			this.loadJsCode();
			// 隐藏panel
			this.$refs.bottomTopPanel.maskClick();
		}
	}
}
